import React from "react";
import { LHS, RHS, FullWidthBtn } from "./general/style/common";
import { BottomTray } from "./general/style/style";
import ProductThumbnail from "./ProductThumbnail";
import { DhProperties } from "../DhProperties";

export default function ProductCompareTray({ modelLeft, modelRight, onClick , clearSelectedModels}) {
  
  const onClickApply = () => {
    try {
      // check for iOS to be added
      if (typeof newsHuntAction != "undefined" && modelLeft && modelRight) {
        const url =
          DhProperties.properties.pwaBaseUrl +
          "/webItem?webModel=" +
          encodeURIComponent(
            JSON.stringify({
              title: "Compare",
              url:
                DhProperties.properties.baseUrl +
                `/compare?product=${modelRight.product}&models=${modelLeft.id},${modelRight.id}`,
            })
          );
          typeof(clearSelectedModels)!="undefined" && clearSelectedModels();
        newsHuntAction.handleAction(url);
      } else if (modelLeft && modelRight) {
        const modelLeftTemp = modelLeft;
        const modelRightTemp = modelRight;
        typeof(clearSelectedModels)!="undefined" && clearSelectedModels();
        window.location =
          DhProperties.properties.baseUrl +
          `/compare?product=${modelLeft.product}&models=${modelLeftTemp.id},${modelRightTemp.id}`;
      }
    } catch (err) {
      console.log("ProductCompareTray: ", err);
    }
  };

  return (
    <BottomTray padding="12px 0 12px 0" className='BottomTray'>
      <div className={(modelLeft && modelLeft.product=="mobile")||(modelRight && modelRight.product=="mobile")? "compare" : "compare laptops"}>
        <LHS><ProductThumbnail itemSelected={modelLeft} onClick={onClick} /></LHS>
        <RHS><ProductThumbnail itemSelected={modelRight} onClick={onClick} /></RHS>
      </div>
      {!(modelLeft && modelRight) ? (
        <FullWidthBtn className="FullWidthBtn">Select another product to Compare</FullWidthBtn>
      ) : (
        ""
      )}
      {modelLeft && modelRight ? (
        <FullWidthBtn className="active" onClick={() => onClickApply()}>
          Apply
        </FullWidthBtn>
      ) : (
        ""
      )}
    </BottomTray>
  );
}



